<template>
  <!-- 登录才可以查看打开购物车 -->
  <div class="shopcart">
    <header>
      <van-nav-bar
        title="购物车"
        :right-text="loginStatus.txt"
        left-arrow
        @click-left="back"
        @click-right="manager"
        :fixed="true"
      />
    </header>

    <div class="shopcartbox">
      <!-- 未登录 -->
       <div v-if="!loginStatus.isLogin">
          <div class="not-login" @click="changePage({name: 'login'})">请登录</div>
       </div>

      <!-- 登录 -->
      <div v-else>
        <!-- 登录且存在商品数据 -->
        <div v-if="shopcartData.length > 0">
          <van-swipe-cell :data-id="item.pid" :disabled="!isEdit" v-for="(item, index) in shopcartData" :key="index">
            <div class="item-box">
              <van-row class="item">
                <van-col class="item-checkbox" span="2">
                  <van-checkbox v-model="item.isCheck" @change="simpleSelect()"></van-checkbox>
                </van-col>
                <van-col span="22" offset="2">
                  <van-row>
                    <van-col span="8">
                      <img class="autoimg" :src="imgBaseUrl + item.pimg" alt="">
                    </van-col>
                    <van-col class="item-info" span="16">
                      <div class="item-name two-txt">{{item.pname}}</div>
                      <!-- <div class="item-price clearfix" v-show="!isEdit"> -->
                      <div class="item-price clearfix">
                        <span class="price fl">&yen; {{item.p_price}}</span>
                        <span class="count fr">x{{item.count}}</span>
                      </div>
                      <div class="modify-count">
                        <van-stepper input-width="32px" button-size="26px" v-model="item.count" @change="modifyCount({count: item.count, userId: item.user_id, pid: item.pid, date: item.created_at})" />
                      </div>
                      <div v-show="isEdit" class="del">
                        <van-button type="danger" size="small" @click="delShopcartPro({userId: item.user_id, pid: item.pid, date: item.created_at}, index)">删除</van-button>
                      </div>
                    </van-col>
                  </van-row>
                </van-col>
              </van-row>
            </div>
            <!-- <template slot="right">
              <van-button square type="danger" style="height: 100%;" text="删除" @click="delShopcartPro({userId: item.user_id, pid: item.pid, date: item.created_at}, index)"/>
            </template> -->
          </van-swipe-cell>

          <van-submit-bar
            :price="submitInfo.price"
            button-text="提交订单"
            @submit="onSubmit"
            :safe-area-inset-bottom="true"
            v-if="shopcartData.length > 0"
            v-show="!isEdit"
            :disabled="submitInfo.price == 0">
            <van-checkbox v-model="submitInfo.allCheck" @click="allSelect">全选</van-checkbox>
          </van-submit-bar>

          <div class="all-del">
            <van-submit-bar
              button-text="全部删除"
              :price="submitInfo.price"
              :safe-area-inset-bottom="true"
              v-show="isEdit"
              :disabled="!submitInfo.allCheck"
              @submit="delAllshopcartPros">
              <van-checkbox v-model="submitInfo.allCheck" @click="allSelect">全选</van-checkbox>
            </van-submit-bar>
          </div>
        </div>
        <!-- <div v-if="shopcartData.length > 0">
          <van-swipe-cell>
            <div class="item-box">
              <van-row class="item">
                <van-col class="item-checkbox" span="2">
                  <van-checkbox></van-checkbox>
                </van-col>
                <van-col span="18" offset="2">
                  <van-row>
                    <van-col span="8">
                      <img class="autoimg" src="http://img0.imgtn.bdimg.com/it/u=3024308660,1355431406&fm=26&gp=0.jpg" alt="">
                    </van-col>
                    <van-col class="item-info" span="16">
                      <div class="item-name one-txt">商品品名名称</div>
                      <div class="item-price clearfix">
                        <span class="price fl">&yen;12.4</span>
                        <span class="count fr">x2</span>
                      </div>
                      <div class="modify-count">
                        <van-stepper/>
                      </div>
                    </van-col>
                  </van-row>
                </van-col>
              </van-row>
            </div>

            <template slot="right">
              <van-button square type="danger" style="height: 100%;" text="删除" />
              <van-button square type="primary" style="height: 100%;" text="收藏"/>
            </template>
          </van-swipe-cell>

          <van-submit-bar
            :price="submitInfo.price"
            button-text="提交订单"
            @submit="onSubmit">
            <van-checkbox v-model="submitInfo.allCheck">全选</van-checkbox>
          </van-submit-bar>
        </div> -->

        <!-- 登录，购物车没有数据 -->
        <div class="not-product" v-if="loginStatus.isLogin && shopcartData.length == 0">购物车空空如也!</div>
      </div>

      <!-- 推送商品 -->
      <!-- <div class="recommend clearfix" v-if="!loginStatus.isLogin || loginStatus.isLogin && shopcartData.length == 0"> -->
      <div class="recommend clearfix">
        <div class="title clearfix">
          <span class="fl">精选推荐</span>
          <span class="fr">更多
            <i></i>
          </span>
        </div>
        <div class="list clearfix">
          <div class="col-box fl" v-for="(item,index) in RecommendData" :key="index" @click="previewProDetail(item.pid)">
            <div class="col-item">
              <div>
                <img class="autoimg" :src="item.img" alt="">
              </div>
              <div class="two-txt pro-title">{{item.title}}</div>
              <div class="price">&yen;{{item.price}}</div>
            </div>
          </div>
          <!-- <div class="col-box fl">
            <div class="col-item">
              <div>
                <img class="autoimg" src="http://img0.imgtn.bdimg.com/it/u=3024308660,1355431406&fm=26&gp=0.jpg" alt="">
              </div>
              <div class="two-txt pro-title">张艺兴（LAY），1991年10月7日出生于湖南省长沙市，内地流行男歌手艺兴（LAY），1991年10月7日出生于湖南省长沙市，内地流</div>
              <div class="price">&yen;1</div>
            </div>
          </div> -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'shopcart',
    data(){
      return{
        submitInfo:{
          price:0, //总价 （之前创建表时写的价格是pPrice在表中以及获取的时候是p_price）
          allCheck:false
        },

        loginStatus:{
          isLogin:true,
          txt:'管理'
        },

        shopcartData:[], // 购物车数据
        imgBaseUrl:'http://127.0.0.1:10008/assets/',
        isEdit: false, //是否处于编辑状态

        // 推荐自定义数据
        RecommendData:[
          {
            title:'百颗全蔬360g】综合果蔬菜脆 水果干混合装网红零食',
            img: require('../assets/images/limited1.png'),  //本地图片引入要添加require
            price:59,
            pid:'cmXmNM1580568554258'
          },
          {
            title:'风干牛肉干50g】内蒙古特产年货手撕牛肉休闲小吃',
            img: require('../assets/images/limited2.png'),
            price:29,
            pid:'gDrCK71580568687579'
          },
          {
            title:'德芙礼盒装丝滑牛奶纯黑白夹心3碗排块送女友小零食喜糖果',
            img: require('../assets/images/limited3.png'),  
            price:14,
            pid:'ISLoDB1580568785381'
          },
          {
            title:'雪芙蕾蛋糕1000g整箱】营养早餐网红糕点小面包',
            img: require('../assets/images/limited4.png'),
            price:16,
            pid:'mhYFMQ1580568838312'
          },
          {
            title:'猪肉脯100g】麻辣零食熟食风干靖江特产小吃肉干',
            img: require('../assets/images/limited5.png'),
            price:9,
            pid:'URDHHr1580568898228'
          },
          {
            title:'百颗全蔬360g】综合果蔬菜脆 水果干混合装网红零食',
            img: require('../assets/images/limited1.png'),  //本地图片引入要添加require
            price:59,
            pid:'cmXmNM1580568554258'
          },
          {
            title:'风干牛肉干50g】内蒙古特产年货手撕牛肉休闲小吃',
            img: require('../assets/images/limited2.png'),
            price:29,
            pid:'gDrCK71580568687579'
          },
          {
            title:'德芙礼盒装丝滑牛奶纯黑白夹心3碗排块送女友小零食喜糖果',
            img: require('../assets/images/limited3.png'),  
            price:14,
            pid:'ISLoDB1580568785381'
          },
          {
            title:'雪芙蕾蛋糕1000g整箱】营养早餐网红糕点小面包',
            img: require('../assets/images/limited4.png'),
            price:16,
            pid:'mhYFMQ1580568838312'
          },
        ] 

      }
    },

    created(){
      // 获取验证登录的token
      let utk = this.$cookies.get('utk');
      // console.log('utk ==> ', utk);

      if(utk == null){
        this.loginStatus.isLogin = false;
        this.loginStatus.txt = '';
      }else{
        // 进行登录验证 查询出购物车数据
        this.axios({
          method:'GET',
          url:'/findShopcartData',
          params: {
            key: 'default'
          }
        }).then( result =>{
          // console.log('findShopcartData result=>',result);
          if(result.data.status == 4000){

            result.data.data.forEach(v => {
              v.isCheck = false;
            });

            this.shopcartData = result.data.data.concat();
            // console.log('this.shopcartData=》',this.shopcartData);
          
          }
          this.loginStatus.txt = this.shopcartData.length > 0 ? '管理' : '';

        }).catch( err =>{
          // console.log('客户端登录出错！');
        })
      }
    },
    
    methods:{
      // 基本页面和基本绑定事件back、manager、onSubmit需要后台创建购物车表shopcart.js
      // 返回
      back(){
        this.$router.go(-1);
      },

      changePage(o){
        this.$router.push(o);
      },

      // 点击跳转预览商品详情 (通过id辨识具体商品)
      previewProDetail(id){
        this.$router.push({name:'detail',params:{id}});
      },
      
      // 管理完成购物车
      manager() {
        if(!this.isEdit) {
          this.loginStatus.txt = '完成';
        }else {
          this.loginStatus.txt = '管理';
        }
          this.isEdit = !this.isEdit;
      },

      // 全选
      allSelect(){
          this.shopcartData.forEach(v => {
          v.isCheck = !this.submitInfo.allCheck;
        })
        
        this.calcTotalPrice(); //计算订单总价
      },

      // 单选
      simpleSelect(){

        this.calcTotalPrice(); //重新计算订单总价

        for (let i = 0; i < this.shopcartData.length; i++) {
          if (!this.shopcartData[i].isCheck) {

            this.submitInfo.allCheck = false;
            return;
          }
        }
        this.submitInfo.allCheck = true;
      },

      //计算总价格
      calcTotalPrice() {
        this.submitInfo.price = 0
        this.shopcartData.forEach(v => {
          if (v.isCheck) {
            this.submitInfo.price += v.p_price * v.count * 100;
          }
        })
      },

      //修改数量,
      // 条件：登录验证，用户id，商品id，商品状态，添加商品时间
      modifyCount(o) {
        // console.log('o ==> ', o);

        o.key = 'default';
        this.axios({
          method:'POST',
          url:'./modifyShopcartProCount',
          data:o
        }).then( result =>{
          // console.log('modifyCount result=>',result);

          if(result.data.status == 30002){

            this.calcTotalPrice(); //重新计算订单总价
          }
        }).catch( err=>{
          // console.log('出错');
        })

      },

      // 删除单个商品
      delShopcartPro(o, index){
        // this.shopcartData.splice(index, 1); //测试删除
        o.key = 'default'; //标记
        this.axios({
          method:'POST',
          url:'./delShopcartPro',
          data:o
        }).then( result =>{            
          // 这边只能收到一个res.send的数据，因为后台的同个作用域代码中只能有一个作用的res.send字段
          // console.log('delShopcartPro result ==> ', result);
            
            if (result.data.status == 30004) {
              this.shopcartData.splice(index, 1); //删除
            }

        }).catch( err =>{
          // console.log('出错');
        })
      },

      // 清空购物车
      delAllshopcartPros(){
        //后台所需参数
        let data = {
          userId: this.shopcartData[0].user_id,
          proData:[], //用数组来存放删除的商品
          key:'default'
        };

        this.shopcartData.forEach(v =>{
          // 根据商品的id来删除
          let o = {
            pid:v.pid,
            createdAt:v.created_at
          };

          data.proData.push(o);
        })

        data.proData = JSON.stringify(data.proData);
        // console.log('清空购物车data=>',data);

        this.axios({
          method:'POST',
          url:'./delAllshopcartPros',
          data
        }).then( result =>{
          // console.log('result =>',result);

          if(result.data.status == 30004){

            this.shopcartData = []; //删除页面所有商品
            this.submitInfo.allCheck = false;  //取消全选的勾选
          }
        }).catch( err=>{
          // console.log('err=>',err);
        })
      },

      //提交订单
      onSubmit() {
        // console.log('提交订单');

        let data = {
          userId:this.shopcartData[0].user_id,
          proData:[], //提交的商品的数据
          key:'default'
        };

        this.shopcartData.forEach( v=>{
          if(v.isCheck){
            let o = {
              pid:v.pid,
              createdAt:v.created_at
            };

            data.proData.push(o);
          }
        })

        data.proData = JSON.stringify(data.proData);
        // console.log('提交订单data =>',data);

        this.axios({
          method:'POST',
          url:'./submitOrders',
          data // == data:{key:'default'}上面已经定义好了
        }).then(result =>{
          // console.log('提交订单result =>',result);

          if(result.data.status == 40000){
            for(let i = this.shopcartData.length - 1; i>=0; i++){
              if(this.shopcartData[i].isCheck){
                this.shopcartData.splice(i,1);
              }
            }
          }

        }).catch( err =>{
          // console.log('err =>','提交订单出错');
        })
      }
      // transaction(fn){添加事务处理方法，并且给创建方法、更新方法也添加事务对象}
      //  createData(modelName, o,t) {return 。。。create(o,{transaction:t});}
      // updateData(modelName, attributeValues, condition, t) {}
    }

  }
</script>

<style lang="less" scoped>
  .shopcart{
    /deep/.van-checkbox__icon .van-icon {
      width: 1em;
      height: 1em;
    }
    /deep/.van-icon-success:before{
      font-size: .76em;
    }

    .van-submit-bar{
      bottom: 50px;
    }
    /deep/.van-checkbox__icon{
      height: 100%;
    }

    .all-del{
      .van-submit-bar__text {
        opacity: 0;
      }
    }


    header{
        width: 100%;
        height: 50px;
        background-color: #f5f5f5;
        /deep/.van-nav-bar{
          background-color: transparent;
        }
        .van-nav-bar__text{
          color: #000;
          font-size: 16px;
        }
        .van-nav-bar__title{
          color: #000;
          font-size: 18px;
        }
        .van-icon-arrow-left:before{
           color: #000;
           font-size: 20px;
        }
    }

    .shopcartbox{
      position: fixed;
      top: 50px;
      bottom: 81px;
      left: 0;
      right: 0;
      overflow-y: auto;
    }
    .not-login{
      width: 80px;
      height: 34px;
      color: #ddd;
      margin: 10px auto;
      line-height: 34px;
      text-align: center;
      font-size: 14px;
    }
    .not-product{
      font-size: 18px;
      color: #666;
      text-align: center;
      margin: 10px auto;
    }

    .van-card{
      background-color: transparent;
      border: 1px solid #ddd;
    }

    .col-item{
      background-color: #fff;
      padding: 5px;
    }
    .item-box{
      .item{
        position: relative;
        padding: .267rem;
        margin-bottom: .133rem;
        background-color: #fff;
      }
      .item-checkbox{
        position: absolute;
        top: 50%;
        left: 8px;
        transform: translateY(-50%);
      }
      .item-name{
        font-size: 15px;
        height: 38px;
        padding: 4px 0;
        width: calc(~'100% - 44px');
        text-align: center;
      }
      .del{
          position: absolute;
          right: 0;
          top: 50%;    
          transform: translateY(-50%);
        }
      .item-info{
        padding: 0 15px;
        // background-color: red;
        position: relative;
      }
      .item-price{
        padding: 6px 0;
      }
      .price{
        font-size: 14px;
        color: #e4393c;
        margin-right: 20px;
        font-weight: bold;
        margin: 2px 0;
      }
      .count{
        color: #666;
        font-size: 14px;
      }
    }

    // 商品推送
    .title{
      height: 24px;
      line-height: 24px;
      background-color: #fff;
      padding: 6px;
      >span{
        &:first-child{
          font-size: 18px;
          font-weight: bold;
        }
        &:last-child{
          font-size: 15px;
        }
        >i{
          float: right;
          display: block;
          width: 24px;
          height: 24px;
          background: url("../assets/images/right.png")no-repeat center center;
          background-size:18px 18px;         
        }
      }
    }

    .recommend{
      margin: 20px auto;
      .list{
        padding: 0 4x;
      }
      .col-box{
        padding-right: 5px;
        width: calc(~"100% / 3 - 5px + 5px / 3");
        margin-bottom: 5px;
        .col-item{
          padding: 6px;
          background-color: #fff;

          .pro-title{
            margin-top: 8px;
            font-size: 13px;
            height: 32px;
            text-align: center;
          }
          .price{
            margin-top: 8px;
            color: #F50301;
            font-size: 16px;
            font-weight: bold;
            text-indent: 10px;
          }
        }
      }
      .col-box:nth-child(3n){
        padding-right: 0;
      }
    }
  }
</style>